<template>
  <div style="height: 100vh ; overflow: auto" @scroll="onScroll">
    <TopNav/>
    <div class="px-20 w-full flex flex-wrap">
      <template v-for="xx in widthDiv" :key="xx">
        <div   :class="width">
        <template v-for="subArray in dynaCard" :key="subArray.id">
            <div  v-for="(arr, index) in subArray[xx-1]" :key="index" class="w-full">
              <div class="w-full p-2">
                <Card :src="`${arr.image_url}`" :board="`${arr.domain}`" class="w-full h-full"/>
                <div class="text-sm leading-tight pt-2">
                  <p class="font-bold"> {{ arr.name }} </p>
                  <p> {{ arr.price }} </p>
                </div>
              </div>
            </div>
        </template>
          </div>
      </template>
    </div>
  </div>
</template>

<script>
import TopNav from './TopNav.vue';
import Card from './Card.vue';

const axios = require('axios');
export default {
  name: 'App',
  components: {
    TopNav,
    Card
  },
  created() {
    // window.addEventListener("resize", this.changeSize);
  },
  unmounted() {
    // window.removeEventListener("resize", this.changeSize);
  },
  mounted() {
    this.getData()
    this.changeSize()
  },
  methods: {
    changeSize() {
      if (window.innerWidth > 800) {
        this.width = "w-1/3"
        this.widthDiv = 3
      }
      if (window.innerWidth < 800) {
        this.width = "w-1/2"
        this.widthDiv = 2
      }
      if (window.innerWidth > 1300) {
        this.width = "w-1/5"
        this.widthDiv = 5
      }
    },
    addDyna() {
      this.input.push({name: "okay"})
    },
    getData() {
      axios.get("https://xoosha.com/ws/1/test.php?offset=" + this.offset).then(r => {
        r.data.map(d => this.cards.push({image_url: d.image_url, name: d.name, price: d.price, domain: d.domain}))
        console.log(this.cards.length)

        const length = Math.ceil(this.cards.length / this.widthDiv);
        let x = new Array(this.widthDiv)
            .fill()
            .map(() => {
              return this.cards.splice(0, length)
            })
        this.dynaCard.push(x)
      })
    },
    onScroll({target: {scrollTop, clientHeight, scrollHeight}}) {
      if (scrollTop + clientHeight >= scrollHeight) {
        this.offset += 60
        this.getData()
      }
    }
  },
  data() {
    return {
      cards: [],
      offset: 0,
      width: "w-1/5",
      widthDiv: 5,
      dynaCard: [],
      fake: [{id: "1"}, {id: "2"}, {id: "3"}, {id: "4"}, {id: "5"}]
    }
  }
}
</script>

<style src="./assets/tailwind.css"/>
